.resultsFiltersConteiner {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
}

.resultsFilters {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.resultsFilter {
  line-height: 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 2px;
}

.resultsFilter input[type="radio"] {
  appearance: none;
  background-color: transparent;
  margin: 0;
  font: inherit;
  color: #d7127c;
  width: 1rem;
  height: 1rem;
  border: 2px solid #d7127c;
  border-radius: 50%;
  display: grid;
  place-content: center;
}

.resultsFilter input[type="radio"]::before {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  background-color: #d7127c
}

.resultsFilter input[type="radio"]:checked::before {
  transform: scale(1);
}

@media (max-width: 991.98px) {
  .resultsFiltersConteiner {
    font-size: 1.2rem;
  }

  .resultsFilters {
    width: 35%;
  }
}

@media (max-width:767.98px) {
  .resultsFilters {
    width: 50%;
  }
}

@media (max-width:576px) {
  .resultsFilters {
    width: 80%;
  }
}