.recipeBoxesContainer {
  padding: 0 7rem;
  margin-bottom: 2rem;
}

.recipeHeader {
  font-size: 2.5rem;
  padding: 1.5rem 0;
  border-bottom: 3px solid #d7127c;
  border-top: 3px solid #d7127c;
}

.recipesBoxes {
  margin-top: 2rem;
}

.recipesBoxes ul li a {
  text-decoration: none;
  color: #000;
}


.arrow {
  width: 50px;
  height: 50px;
  background: rgba(215, 18, 124, 0.8);
  border: none;
  padding: 0;
}

.arrow svg {
  fill: #fff;
}

@media (max-width: 991.98px) {
  .recipeBoxesContainer {
    padding: 0 5rem;
  }

  .recipeHeader {
    font-size: 2rem;
  }
}

@media (max-width:767.98px) {
  .recipeBoxesContainer {
    padding: 0 3rem;
  }
}

@media (max-width:576px) {
  .recipeBoxesContainer {
    padding: 0 2rem;
  }
}