.recipeContainer {
  padding: 0 7rem;
}

.recipeheader {
  font-size: 2.5rem;
  padding: 1.5rem 0;
  border-bottom: 3px solid #d7127c;
  border-top: 3px solid #d7127c;
}

.recipeInfo {
  margin: 2rem 0;
  display: flex;
  justify-content: space-between;
}

.recipeInfo img {
  width: 50%;
  max-width: 500px;
}

.recipeInfo .recipeingridients {
  width: 40%;
}

.recipeInfo .recipeingridients h2,
.recipeinstruction h2 {
  border-bottom: 2px dashed #d7127c;
  margin: 0;
}

.recipeInfo .recipeingridients ul {
  padding-left: 2rem;
  text-align: start;
  font-size: 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.recipeInfo .recipeingridients ul li::marker {
  color: #d7127c;
}

.recipeinstruction p {
  font-size: 1.5rem;
}

@media (max-width: 991.98px) {
  .recipeContainer {
    padding: 0 5rem;
  }

  .recipeheader {
    font-size: 2rem;
  }

  .recipeInfo .recipeingridients ul {
    font-size: 1.3rem;
  }
}

@media (max-width:767.98px) {
  .recipeInfo .recipeingridients ul {
    grid-template-columns: 1fr;
  }

  .recipeContainer {
    padding: 0 5rem;
  }
}

@media (max-width:576px) {
  .recipeContainer {
    padding: 0 3rem;
  }

  .recipeInfo {
    flex-direction: column;
    margin-bottom: 0;
  }

  .recipeInfo img {
    width: 100%;
  }

  .recipeInfo .recipeingridients {
    width: 100%;
    margin-top: 2rem;
  }
}