.navbar {
  height: 15vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 7rem;
}

.logo img {
  width: 140px;
  height: 80px;
}

@media (max-width: 991.98px) {
  .navbar {
    padding: 0 5rem;
  }
}

@media (max-width:767.98px) {
  .navbar {
    padding: 0 3rem;
  }

  .searchComponent input {
    width: 60%;
  }
}

@media (max-width:576px) {
  .logo img {
    width: 100px;
    height: 60px;
  }

  .navbar {
    height: 20vh;
    flex-direction: column;
    justify-content: flex-start;
  }
}