.dietContainer {
  padding: 0 7rem;
}

.dietHeader {
  font-size: 2.5rem;
  padding: 1.5rem 0;
  border-bottom: 3px solid #d7127c;
  border-top: 3px solid #d7127c;
}

.dietContent {
  margin-top: 2rem;
  display: flex;

}

.dietFilters {
  width: 20%
}

.dietFilters a {
  text-decoration: none;
  color: #000;
  transition: color 0.3s ease-in-out;
}

.dietFilters a.active {
  color: #d7127c;
  text-decoration: underline #d7127c;
}

.dietFilters a:hover {
  color: #d7127c;
}

.dietRecipeResults {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.dietitem {
  width: 100%;
}

.dietitem a {
  color: #000;
  text-decoration: none;
}

@media (max-width: 991.98px) {
  .dietContainer {
    padding: 0 5rem;
  }

  .dietHeader {
    font-size: 2rem;
  }

  .dietRecipeResults {
    grid-template-columns: repeat(3, 1fr);
  }
}


@media (max-width:767.98px) {
  .dietContainer {
    padding: 0 3rem;
  }

  .dietRecipeResults {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width:576px) {
  .dietContainer {
    padding: 0 2rem;
  }

  .dietContent {
    flex-direction: column;
    align-items: center;
  }

  .dietFilters {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 30%);
    justify-content: space-between;
    align-items: center;
  }

  .dietFilters h2 {
    margin-top: 0;
  }

  .dietRecipeResults {
    width: 100%;
    grid-template-columns: 1fr;
  }
}