.dietPickerContainer {
  background-color: #d7127c;
  padding: 1rem 0;
  display: flex;
  padding: 1rem 7rem;
  margin-bottom: 2rem;
}


.dietPickerHeader {
  font-size: 2rem;
  color: #fff;
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dietPickerHeader h2 {
  margin: 0;
}

.dievPickerCategories {
  width: 80%;
  display: flex;
  justify-content: space-around;
  color: #fff;
}

.dietItem {
  font-weight: bold;
  text-align: center;
  margin-top: 1rem;
}

.dietItem a {
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;

}

.dietItem img {
  width: 100px;
  height: 100px;
}

@media (max-width: 991.98px) {
  .dietPickerHeader {
    font-size: 1.5rem;
  }

  .dietPickerContainer {
    padding: 0.7rem 5rem;
  }

  .dietItem img {
    width: 80px;
    height: 80px;
  }
}

@media (max-width:767.98px) {
  .dietPickerHeader {
    font-size: 1.5rem;
    width: 80%;
    margin-bottom: 1rem;
  }

  .dietPickerContainer {
    flex-direction: column;
    align-items: center;
    padding: 0.4rem 3rem;
  }

  .dievPickerCategories {
    width: 100%;
  }

  .dietItem img {
    width: 80px;
    height: 80px;
  }
}

@media (max-width:600px) {
  .dievPickerCategories {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}