.resultsContainer {
  padding: 0 7rem;
}

.resultsHeader {
  font-size: 2.5rem;
  padding: 1.5rem 0;
  border-bottom: 3px solid #d7127c;
  border-top: 3px solid #d7127c;
}

.resultsContent {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.resultsContent a {
  text-decoration: none;
  color: #000;
}

.notFound {
  font-size: 2rem;
}

@media (max-width: 991.98px) {
  .resultsContainer {
    padding: 0 5rem;
  }

  .resultsHeader {
    font-size: 2rem;
  }

  .resultsContent {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width:767.98px) {
  .resultsContainer {
    padding: 0 3rem;
  }

  .resultsContent {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width:576px) {
  .resultsContent {
    grid-template-columns: 1fr;
  }
}